.flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.central {
  padding-bottom: 30px;
}
@media (min-width: 450px) {
  .central {
    max-width: 500px;
    -webkit-box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.5);
            box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.5);
    margin-top: 100px;
    padding: 20px;
    padding-bottom: 30px;
  }
}
.h1-icon {
  font-size: 34.8px;
  text-shadow: none;
}
.title-h1 {
  margin-bottom: 50px;
  text-shadow: 0px 2px 3px orange;
  font-size: 3.1em;
}
.bounce {
  -webkit-animation: bounce 1s linear 1s infinitE;
          animation: bounce 1s linear 1s infinitE;
  position: relative;
  background: pink;
  border-radius: 50%;
  display: inline-block;
}
.bounce:before,
.bounce:after {
  content: '';
  position: absolute;
  top: 18.6px;
  left: 16.3px;
  height: 3px;
  width: 3px;
  background: #fff;
  border-radius: 50%;
  -webkit-animation: glow 0.5s linear infinite;
          animation: glow 0.5s linear infinite;
}
.bounce:after {
  right: 18.9px;
  left: auto;
}
@-webkit-keyframes bounce {
  from {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  to {
    -webkit-transform: translatey(20px);
            transform: translatey(20px);
  }
}
@keyframes bounce {
  from {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  to {
    -webkit-transform: translatey(20px);
            transform: translatey(20px);
  }
}
@-webkit-keyframes glow {
  0% {
    -webkit-box-shadow: 0 0 0px red;
            box-shadow: 0 0 0px red;
  }
  100% {
    -webkit-box-shadow: 0 0 20px red;
            box-shadow: 0 0 20px red;
  }
}
@keyframes glow {
  0% {
    -webkit-box-shadow: 0 0 0px red;
            box-shadow: 0 0 0px red;
  }
  100% {
    -webkit-box-shadow: 0 0 20px red;
            box-shadow: 0 0 20px red;
  }
}
.pointer {
