}
.clear,
.search-counter {
  position: absolute;
  top: -5px;
  right: 0;
  color: red;
  cursor: pointer;
}
.clear {
  top: 10px;
}
.text-field-width {
  width: 80% !important;
}
.search-counter {
  right: 30px;
  top: -4px;
  width: 30px;
  height: 30px;
  background: grey;
  color: white;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  opacity: 0;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.visibleCounter {
  opacity: 1;
}
